.skill {
    min-width: 200px;
    width: 350px;
    height: 300px;
    background-color: #dce0df;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 5px;
    margin: 5px;
}

.icon {
    min-height: 60px;
    width: 60px;
    background-color: dimgray;
    margin: 10px;
}

.description {
    text-align: center;
    padding: 8px;
    overflow: auto;
}

@media screen and (max-width: 830px) {
    .skill {
        width: 480px;
        /*font-size: 25px;*/
    }
}