.footer {
    width: 100%;
    padding: 20px 0;
}

.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid;
}

.contentWrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.content {
    width: 250px;
    height: 80px;
    border: 1px solid;
    text-align: center;
    margin: 20px 0 20px 0;
}